.bottomportfolio{
    display: flex;
    flex-direction: column;
    color: black;
    justify-content: center;
    margin: 5px 0;
    align-items: center;
}

.bottomportfolio > h1{
    padding-bottom: 20px;
}

.bottomportfolio > h4{
    padding-bottom: 20px;
}

.bottomportfolio__box{
    color: antiquewhite;
    height: 30px;
    width: 100vh;
    box-shadow: inset 0 0 0  rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: space-evenly;
    transition: transform 200ms ease-in-out;
}

.bottomportfolio__box:hover{
    transform: scale(1.09);
}