@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');


.blog{
    align-items: center;
    justify-content: center;
    margin: 10px 30px;
    font-family: 'Roboto', sans-serif;
    text-align: center;
    display: grid;
    row-gap: 10px;
    color: rgb(5, 5, 4);
}


.blog1{
    width: 250px;
    height: 300px;
    background-color: rgb(248, 248, 248);
    font-family: 'Roboto', sans-serif;
    border-radius: 10px;
    box-shadow: 0 3px 10px rgba(104, 104, 104, 0.938);
    background-image: url(https://tse2.mm.bing.net/th?id=OIP.WNEGInbVc_nJSEwY98BQcgHaEs&pid=Api&P=0&w=266&h=168);
    background-position: bottom;
    background-repeat: no-repeat;
    font-size: 13px;
    margin-top: 30px;
    transition: transform 200ms ease-in-out;
}

.blog1:hover{
    transform: scale(1.09);
}

.blog1 > p{
    align-items: center;
    justify-content: center;
    margin: 2px 10px;
    font-weight: 800;
}

.blog1 > h3{
    margin-top: 1%;
    margin: 20px 10px;
    font-weight: 1500;
}

