@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');


.middleabout{
    align-items: center;
    justify-content: center;
    margin: 10px 30px;
    font-family: 'Roboto', sans-serif;
    text-align: center;
    display: grid;
    row-gap: 10px;
    color: antiquewhite;
}


.design{
    width: 250px;
    height: 300px;
    background-color: rgb(57, 73, 215);
    font-family: 'Roboto', sans-serif;
    border-radius: 30px;
    box-shadow: 0 3px 10px rgba(14, 10, 244, 0.938);
    transition: transform 200ms ease-in-out;
}

.design:hover{
    transform: scale(1.09);
}

.design > p{
    align-items: center;
    justify-content: center;
    margin: 60px 60px;
    font-weight: 800;
}

.design > h3{
    margin-top: 5%;
    margin: 3px 80px;
    font-weight: 1500;
}

.design > img{
    max-width: 100px;
    margin: 1px 60px;
}

