@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

.topportfolio{
    margin: 100px 150px;
    font-family: 'Roboto', sans-serif;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    margin-left: 10%;
}

.topportfolio__grid0 > h2{
    color: rgb(103, 103, 105);
    font-family: 'Poppins', sans-serif;
    margin-bottom: 7px;
    font-weight: 1500;
    justify-content: center;
    align-items: center;
}

.topportfolio__grid0 > h4{
    color: rgb(103, 103, 105);
    font-family: 'Poppins', sans-serif;
    margin-bottom: 50px;
    justify-content: center;
    align-items: center;
}



.topportfolio__grid1 > img{
    max-width: 450px;
    transition: transform 200ms ease-in-out;
    margin: 0 0;
    cursor: pointer;
}

.topportfolio__grid1 > img:hover{
    transform: scale(1.09);
}

.topportfolio__grid2 > img{
    max-width: 450px;
    transition: transform 200ms ease-in-out;
    margin: 0 0;
    cursor: pointer;
}

.topportfolio__grid2 > img:hover{
    transform: scale(1.09);
}

.actionportfolio{
    display: flex;
    flex-direction: column;
    line-height: 3;
    margin-left: 10%;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
    cursor: pointer;
}

.topportfolio__word{
    display: flex;
    flex-direction: column;
    line-height: 1.5;
    margin-left: 10%;
    justify-content: center;
    align-items: center;
}

.topportfolio__grid0{
    margin-top : 20px;
}