@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

.topabout{
    display: flex;
    flex-direction: column;
    color: black;
    justify-content: center;
    margin: 50px 0;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    margin-top: 300px;
}

.topabout__right > a > button{
    margin-bottom: 30px;
    text-align: left;
    margin-left: 50px;
    border: none;
    border-radius: 20px;
    outline: none;
    color: antiquewhite;
    background-color: blue;
    font-size: 14px;
    padding: 10px 30px;
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
    cursor: pointer;
    margin-right: 20px;
    margin-top: 15%;
    box-shadow: 0 3px 10px rgba(13, 9, 252, 0.938);
    transition: transform 200ms ease-in-out;
}

.topabout__right > a > button:hover{
    transform: scale(1.09);
    font-family: 'Roboto', sans-serif;
    border-radius: 3px;
    letter-spacing: 1px;
    font-size: 18px;
}

.topabout > h1{
    font-family: 'Poppins', sans-serif;
    margin: 5px 0;
}

.topabout > p{
    font-family: 'Poppins', sans-serif;
    margin: 10px 0;
}

.topabout > h3{
    font-family: 'Poppins', sans-serif;
    margin: 10px 0;
    color: blue;
    transition: transform 200ms ease-in-out;
}

.topabout > h3:hover{
    transform: scale(1.09);
    color: crimson;
}

.topabout > h4{
    font-family: 'Poppins', sans-serif;
    margin: 5px 0;
}

.topabout__col{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 100px 0;
}

