@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap');

.experience{
    display: flex;
    flex-direction: column;
    border: 2px dotted rgb(241, 184, 70);
    border-radius: 10px;
    width: 200px;
    height: 150px;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    background-color: rgb(196, 65, 244);
}

.experience > p{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 16px;
    letter-spacing: 1px;
}