.bottomcontent{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 50px 100px;
}


.bottomcontent__image{
    flex: 0.5;
    max-width: 400px;
    margin-right: 350px;
}

.bottomcontent__content{
    flex: 0.5;
    font-family: 'Roboto', sans-serif;
    text-align: center;
    display: grid;
    row-gap: 10px;
}