@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

.topcontent{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 50px 150px;
    font-family: 'Roboto', sans-serif;
    row-gap: 10px;
}

.topcontent__container{
    flex: 0.5;
    color: gray;
}

.topcontent__container > h1{
    font-size: 55px;
}


.topcontent__image{
    flex: 0.5;
    max-width: 500px;
}

.topcontent__container > a > button{
    margin-bottom: 30px;
    text-align: left;
    margin-left: 50px;
    border: none;
    border-radius: 20px;
    outline: none;
    color: antiquewhite;
    background-color: blue;
    font-size: 14px;
    padding: 10px 30px;
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
    cursor: pointer;
    margin-right: 20px;
    margin-top: 15%;
    box-shadow: 0 3px 10px rgba(13, 9, 252, 0.938);
    transition: transform 200ms ease-in-out;
}

.topcontent__container > a > button:hover{
    transform: scale(1.09);
    font-family: 'Roboto', sans-serif;
    border-radius: 3px;
    letter-spacing: 1px;
    font-size: 18px;
}