@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

.header{
    display: flex;
    align-items: center;
    color: blue;
    background-color: whitesmoke;
}

.header__left{
    flex: 0.4;
}

.header__left > h1{
    text-align: center;
    font-family: 'Roboto', sans-serif;
    letter-spacing: 1px;
    padding: 20px 0;
}

.header__left > h1 > span{
    color: #A6A6A5
}

.header__right{
    display: flex;
    flex: 0.6;
    justify-content: space-evenly;
    align-items: center;
}

.header__right > a >h4{
    font-size: 18px;
    cursor: pointer;
    transition: transform 200ms ease-in-out;
    font-family: 'Poppins', sans-serif;
    padding: 20px 0;
    letter-spacing: 1px;
}

.header__right > a >h4:hover{
    transform: scale(1.09);
    background-color:blue;
    color: aliceblue;
    padding: 5px 20px;
    border-radius: 2px;
    letter-spacing: 1px;
    font-family: 'Poppins', sans-serif;
    font-size: 18px;
    cursor: pointer;
}

.header__right__btn{
    background-color: blue;
    color: whitesmoke;
    padding: 5px 20px !important;
    border-radius: 15px;
    cursor: pointer;
    transition: transform 200ms ease-in-out;
    letter-spacing: 1px;
    box-shadow: 0 3px 10px rgba(13, 9, 252, 0.938);

}

.header__right__btn:hover{
    transform: scale(1.09);
    font-family: 'Roboto', sans-serif;
    border-radius: 3px;
    letter-spacing: 1px;
    font-size: 18px;
}