@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

.middlecontents__content{
    align-items: center;
    justify-content: center;
    margin: 100px 150px;
    font-family: 'Roboto', sans-serif;
    text-align: center;
    display: grid;
    row-gap: 10px;
}

.middlecontents__content > h4{
    color: blue;
}

.middlecontents__contentbox{
    margin: 100px 200px;
}

.websiteui{
    width: 250px;
    height: 300px;
    background-color: rgb(238, 180, 93);
    font-family: 'Roboto', sans-serif;
    border-radius: 30px;
    box-shadow: 0 3px 10px rgba(220, 242, 18, 0.938);
}

.websiteui > p{
    align-items: center;
    justify-content: center;
    margin: 10px 40px;
    font-weight: 800;
}

.websiteui > h3{
    margin-top: 50%;
    margin: 30px 80px;
    font-weight: 1500;
}

.websiteui > img{
    max-width: 100px;
    margin: 2px 60px;
}

.middlecontent__btn{
    font-family: 'Roboto', sans-serif;
    border-radius: 20px;
    letter-spacing: 1px;
    font-size: 15px;
    border: none;
    padding: 10px 20px;
    margin: 2px 60px;
    cursor: pointer;
    transition: transform 200ms ease-in-out;
    box-shadow: 0 3px 10px rgb(255, 255, 254);
    font-weight: 900;
}

.middlecontent__btn:hover{
    transform: scale(1.09);
    font-family: 'Roboto', sans-serif;
    border-radius: 20px;
    letter-spacing: 1px;
    font-size: 18px;
}

