.company{
    display: flex;
    margin: 150px 0;
    justify-content: space-evenly;
   
}

.contact__grid1{
    align-items: center;
    justify-content: center;
}

.company > a > img{
    max-width: 100px;
    align-items: center;
    justify-content: space-evenly;
    transition: transform 200ms ease-in-out;
}

.company > a > img:hover{
    transform: scale(1.09);
}